<template>
    <div class="p-d-flex p-jc-end">
        <div class="p-field-checkbox p-mr-2 p-mt-2">
            <p-checkbox
                v-model="selectAll"
                :binary="true"
                @change="selectAllChangeHandler"
            />
            <label for="">Select All</label>
        </div>
        <p-button
            label="Archive"
            class="p-mr-2"
            @click="archiveMessageHandler"
        />
        <p-button label="Appointment Stat" />
    </div>

    <p-fieldset legend="All Messages">
        <p-data-table
            :value="messages"
            responsiveLayout="scroll"
            showGridlines
            class="p-datatable-sm p-mb-1"
            :loading="loading"
            dataKey="id"
            v-model:selection="selectedMessages"
        >
            <p-column
                selectionMode="multiple"
                headerStyle="width: 3em"
            ></p-column>
            <p-column header="Date" field="sentdate" style="width: 150px" />
            <p-column header="Subject" field="subject" />
            <p-column header="From" field="sendBy" />
            <p-column header="Message" field="message" />
            <p-column header="#">
                <template #body="{data}">
                    <p-badge v-if="data.unRead == 1" />
                </template>
            </p-column>
            <p-column header="Action" field="action" style="width: 150px">
                <template #body="{data}">
                    <p-button
                        label="Reply"
                        class="p-button-sm p-mr-1"
                        @click="openReplyDialog(data)"
                    />
                    <p-button label="ENG" class="p-button-sm" />
                </template>
            </p-column>
        </p-data-table>
    </p-fieldset>

    <p-dialog
        v-model:visible="replyDialog"
        :closable="false"
        style="width: 600px"
    >
        <p-fieldset legend="Previous Conversation">
            <p-textarea
                v-model="relatedMessages"
                disabled
                rows="10"
                class="p-mt-1"
                style="width: 100%"
                v-if="isMessageThred"
            />
            <p-button
                label="Show Message Thread"
                v-else
                @click="showMessageThread"
            />
        </p-fieldset>
        <p-fieldset :legend="replyTo" class="p-mt-2">
            <div class="p-field">
                <label>Subject - {{ form.subject }}</label>
                <p-textarea
                    v-model="form.message"
                    rows="4"
                    class="p-mt-1"
                    style="width: 100%"
                />
            </div>
        </p-fieldset>

        <template #footer>
            <p-button label="Cancel" @click="closeReplyDialog" />
            <p-button label="Send" @click="send" :loading="loading" />
        </template>
    </p-dialog>
</template>

<script>
import { inject, onMounted, reactive, ref } from "vue";
import {
    getAllMessages,
    composeMessageV2,
    getRelatedMessges,
    archiveMesage
} from "@/service/patient-message-log";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
import { useToast } from "primevue/usetoast";
import { id } from "date-fns/locale";
import { STATSUS } from "@/constants";
export default {
    setup() {
        const store = useStore();
        const messages = ref([]);
        const selectedMessages = ref([]);
        const loading = ref(false);
        const replyDialog = ref(false);
        const selectAll = ref(false);

        const form = reactive({
            subject: null,
            message: null,
            sendTo: null,
            sentBy: store.state.user.providerNo,
            senderType: 1,
            parent: null,
            mId: null
        });

        const replyTo = ref();

        const openReplyDialog = (data) => {
            replyTo.value = `Reply To ${data.sendBy}`;
            console.log(data);

            form.subject = data.subject;
            form.sendTo = data.pid;
            form.parent = data.parent;
            form.mId = data.id;

            replyDialog.value = true;
            relatedMessages.value = null;
            isMessageThred.value = false;
        };

        const isMessageThred = ref(false);

        const relatedMessages = ref();
        const showMessageThread = async () => {
            isMessageThred.value = true;
            const { relatedMsg } = await getRelatedMessges(form.parent);
            relatedMessages.value = relatedMsg;
        };

        const websocket = inject("websocket");
        const sendLoading = ref(false);
        const send = async () => {
            sendLoading.value = true;
            const { newMessage } = await composeMessageV2({
                ...form
            });

            websocket.send(
                JSON.stringify({
                    sender_id: store.state.user.id,
                    receiver_id: store.state.artCycle.patient.id,
                    terminal: "cycle"
                })
            );

            form.subject = null;
            form.message = null;
            replyDialog.value = false;
            sendLoading.value = false;
            getMessages();
        };

        const getMessages = async () => {
            loading.value = true;
            try {
                const { messageRecords } = await getAllMessages();
                messages.value = messageRecords;
                emitter.emit("message-inbox:get-all-messgae");
            } catch (err) {
                messages.value = [];
            } finally {
                loading.value = false;
            }
        };

        const toast = useToast();

        const setupWebsocket = () => {
            const websocket = inject("websocket");
            websocket.onmessage = (event) => {
                const data = JSON.parse(event.data);
                if (typeof data.data == "string") {
                    const message = JSON.parse(data.data);
                    if (message.terminal == "nlp") {
                        getMessages();
                    }
                }
            };

            websocket.onerror = () => {
                toast.add({
                    severity: "warn",
                    summary: "Chat Server Down",
                    detail: "Instant chat is off at the moment",
                    life: 3000
                });
            };
        };

        const archiveMessageHandler = async () => {
            loading.value = true;
            let ids = selectedMessages.value.map((item) => item.id);
            let { status } = await archiveMesage(ids);
            if (status == STATSUS.ok) {
                getMessages();
            }
        };

        onMounted(() => {
            getMessages();
            setupWebsocket();
            emitter.emit("message-nofication:has-seen");
        });

        return {
            messages,
            selectedMessages,
            loading,
            selectAll,
            replyDialog,
            openReplyDialog,
            replyTo,
            isMessageThred,
            relatedMessages,
            showMessageThread,
            form,
            send,
            sendLoading,
            archiveMessageHandler,
            closeReplyDialog: () => {
                replyDialog.value = false;
            },
            selectAllChangeHandler: (event) => {
                if (selectAll.value) {
                    selectedMessages.value = [...messages.value];
                } else {
                    selectedMessages.value = [];
                }
            }
        };
    }
};
</script>
